@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;600;700&display=swap);
*{padding:0px;margin:0px;font-family:Quicksand;font-weight:500}h1,h2,h3,h4{color:#555}.App{transition:all 0.5s ease}.library-active{margin-left:30%}.footer{display:flex;justify-content:center;width:100%;margin-top:20px}.footer h3{font-size:1rem}.footer strong a{color:#555;font-weight:700}.player{display:flex;flex-direction:column;justify-content:center;align-items:center;min-height:20vh}.time-control{display:flex;width:50%;justify-content:space-between;align-items:center}.time-control input{width:100%;padding:0rem 0rem;-webkit-appearance:none;background-color:transparent;cursor:pointer}.time-control p{margin:1rem}.play-control{display:flex;justify-content:space-between;align-items:center;width:30%;padding:1rem}.play-control svg{cursor:pointer}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;width:16px;height:16px}.track{background:lightblue;width:100%;height:1rem;position:relative;border-radius:1rem;overflow:hidden}.animate-track{background:#ccc;width:100%;height:100%;position:absolute;top:0;left:0;transform:translateX(0%);pointer-events:none}.skip-back,.skip-forward,.play{color:#3b3b3b}@media screen and (max-width: 768px){.time-control{width:95%}.play-control{width:60%}}.song-container{min-height:60vh;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.song-container img{width:20%;border-radius:50%}.song-container h2{font-size:2rem;font-weight:bold}.song-container h3{font-size:1.25rem;font-weight:600}@media screen and (max-width: 768px){.song-container img{width:60%}}.library{position:fixed;top:0;left:0;width:30rem;height:100vh;background-color:white;overflow-y:scroll;overflow-x:hidden;box-shadow:2px 2px 30px 5px #4a4848;transform:translateX(-100%);transition:all 0.5s ease;opacity:0;z-index:999}.library h2{text-align:left;margin:1rem}.library-song{cursor:pointer;display:flex;align-items:center;width:100%;padding:1rem}.library-song img{width:30%}.library-song:hover{background-color:#c8c9ca}.song-description{padding-left:1rem}.song-description h3{font-size:1rem}.song-description h4{font-size:0.75rem}*::-webkit-scrollbar{width:10px}*::-webkit-scrollbar-track{background-color:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(114,114,114,0.5);border-radius:20px;border:transparent}.selected{background-color:lightskyblue}.library-open{transform:translateX(0%);opacity:1}.library-header{display:flex;align-items:center;justify-content:space-between}.library-header .closebtn{margin:1rem;cursor:pointer;color:#3b3b3b}@media screen and (max-width: 426px){.library{width:100vw}}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav h2{font-weight:bold}nav button{outline:none;background-color:transparent;cursor:pointer;border:3px solid #5a5959;padding:0.5rem;transition:all 0.3s ease}nav button:hover{background-color:#5a5959;color:white}

